import { Controller, Control, FieldErrors } from 'react-hook-form';
import { get } from 'lodash';
import { FC, ReactNode } from 'react';
import styled from 'styled-components';
import SearchInputWithIcon from '../form/SearchInputWithIcon';

interface HookTextInputProps {
  name: any;
  control: Control<any>;
  defaultValue?: any;
  errors: FieldErrors;
  label: string;
  type?: string;
  StartIcon?: ReactNode;
}

const HookInputWithIcon: FC<HookTextInputProps> = ({
  name,
  control,
  defaultValue,
  errors,
  ...props
}) => {
  return (
    <Controller
      name={name}
      control={control}
      defaultValue={defaultValue ?? ''}
      render={({ field }) => (
        <div className="flex flex-1 flex-col">
          <SearchInputWithIcon
            {...field}
            {...props}
            error={Boolean(get(errors, name))}
          />
          {Boolean(get(errors, name)) && (
            <ErrorInput>{get(errors, name)?.message}</ErrorInput>
          )}
        </div>
      )}
    />
  );
};

export default HookInputWithIcon;

const ErrorInput = styled.span.attrs({ className: 'errorMessageFont' })`
  font-size: 12px;
  color: rgba(255, 0, 0, 0.7);
  padding-left: 4px;
`;
