import { FC } from 'react';
import {BrowserRouter as Router, Routes, Route} from 'react-router-dom';
import EmployeeList from './screens/employeeList/EmployeeList';
import EmployeeRegister from './screens/employeeRegister/EmployeeRegister';
import FacilityRegister from './screens/facilityRegister/FacilityRegister';
import ForgotPassword from './screens/forgotPassword/ForgotPassword';
import Home from './screens/home/Home';
import JobListing from './screens/jobListing/JobListing';
import Login from './screens/login/Login';
import NewPassword from './screens/newPassword/NewPassword';

const App: FC = () => {
  return (
    <Router>
      <Routes>
        <Route path="/" element={<Home/>}/>
        <Route path="/login" element={<Login/>}/>
        <Route path="/forgetpassword" element={<ForgotPassword/>}/>
        <Route path="/newpassword" element={<NewPassword/>}/>
        <Route path="/employeeregister" element={<EmployeeRegister/>}/>
        <Route path="/faciltyregister" element={<FacilityRegister/>}/>
        <Route path="/employeelist" element={<EmployeeList/>}/>
        <Route path="/joblisting" element={<JobListing/>}/>
      </Routes>
    </Router>
  );
};

export default App;
