import { FC } from 'react';
import styled from 'styled-components';

const LogosSection: FC = () => {
  return (
    <MainContainer className="mx-auto flex flex-col items-center justify-around xl:flex-row">
      <img src="images/pic1.png" alt="logo1"  className='mb-4 w-1/2 xl:w-auto mt-8 xl:my-12'/>
      <img src="images/pic2.png" alt="logo2" className='my-4 w-1/2 xl:w-auto xl:my-12'/>
      <img src="images/pic3.png" alt="logo3" className='my-4 w-1/2 xl:w-auto xl:my-12'/>
      <img src="images/pic2.png" alt="logo4" className='my-4 w-1/2 xl:w-auto xl:my-12'/>
      <img src="images/pic4.png" alt="logo5" className='mt-4 w-1/2 xl:w-auto mb-8 xl:my-12'/>
    </MainContainer>
  );
};

export default LogosSection;

const MainContainer = styled.div`
  // height: 250px;
  background: #fafafa;
  padding: 0px 150px;
`;
