import { Controller, Control, FieldPath } from 'react-hook-form';
import { FC } from 'react';
import Checkbox from '../form/Checkbox';

interface HookTextInputProps {
  name: any;
  control: Control<any>;
  defaultValue?: any;
}

const HookCheckbox: FC<HookTextInputProps> = ({
  name,
  control,
  defaultValue,
  ...props
}) => {
  return (
    <Controller
      name={name}
      control={control}
      defaultValue={defaultValue ?? false}
      render={({ field }) => (
        <Checkbox checked={field?.value} {...field} {...props} />
      )}
    />
  );
};

export default HookCheckbox;
