import { range } from 'lodash';
import { FC, useState } from 'react';
import styled from 'styled-components';
import { LeftArrowIcon, RightArrowIcon } from '../../../icons';
import { StyledButton, StyledText } from '../../../shared/StyledComponents';
import SingleJob from './SingleJob';

const tabs = [
  {
    id: 1,
    name: 'New',
  },
  {
    id: 2,
    name: 'Applied',
  },
  {
    id: 3,
    name: 'In Talk',
  },
  {
    id: 4,
    name: 'Saved List',
  },
];

const ListContent: FC = () => {
  const [activeTab, setActiveTab] = useState(1);
  return (
    <div className="w-full xl:w-8/12 xl:pl-4">
      <div className="w-full flex items-center mt-12 xl:mt-0 justify-between mb-4">
        <StyledText fs={20} lh={30} ls={0.6} fw={700} color="#DA3B19">
          Job Listings
        </StyledText>
        <div className="flex flex-col sm:flex-row items-center">
          {tabs?.map((val) => (
            <StyledButton
              className="mt-4 sm:mt-0 py-2 px-5 sm:ml-2"
              bg="#F2F2F2"
              key={val.id}
              onClick={() => setActiveTab(val.id)}
            >
              <StyledText
                fw={400}
                color={val.id === activeTab ? '#EA7112' : '#000'}
              >
                {val.name}
              </StyledText>
            </StyledButton>
          ))}
        </div>
      </div>
      <div className="w-full flex flex-1 flex-col mb-8">
        {range(3).map((item, idx) => (
          <SingleJob key={idx} />
        ))}
      </div>
      <div className="flex flex-1 items-center justify-center xl:justify-start">
        <StyledPageBtn>
          <LeftArrowIcon />
        </StyledPageBtn>
        <PageContainer className="mx-4">
          <StyledText fw={400} color="#000">
            1 of 5
          </StyledText>
        </PageContainer>
        <StyledPageBtn>
          <RightArrowIcon />
        </StyledPageBtn>
      </div>
    </div>
  );
};

export default ListContent;

const StyledPageBtn = styled.button`
  width: 60px;
  height: 60px;
  background-color: #ea7112;
  display: flex;
  align-items: center;
  justify-content: center;
`;

const PageContainer = styled.div`
  height: 60px;
  padding: 18px 49px;
  border: 1px solid #ea7112;
  display: flex;
  align-items: center;
  justify-content: center;
`;
