import { FC } from 'react';
import styled from 'styled-components';
import { HomeIcon, LocationIcon, MailIconSm, PhoneIcon } from '../../../icons';
import { StyledButton, StyledText } from '../../../shared/StyledComponents';

const SingleEmployee: FC = () => {
  return (
    <Container>
      <div className="xl:flex pb-7 border-b border-gray-100">
        <div className='w-full xl:w-8/12 flex'>
        <div className="w-1/5 pr-5">
          <StyledImage
            src="https://c4.wallpaperflare.com/wallpaper/587/394/514/anime-narcissu-shinohara-himeko-wallpaper-preview.jpg"
            alt="user"
          />
        </div>
        {/* <div className="flex-1 flex w-full pl-5"> */}
          <div className="w-4/5 pr-11">
            <StyledText fs={20} lh={30} fw={400} color="#000">
              Jessica Madela
            </StyledText>
            <DescriptionContainer>
              <StyledText fs={13} lh={15} fw={400} color="#5D5D5D">
                Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed
                diam nonumy eirmod tempor invidunt ut labore et dolore magna
                aliquyam erat, sed diam
              </StyledText>
            </DescriptionContainer>
            <div className="flex flex-1 items-center mb-5">
              <span className="mr-1">
                <LocationIcon />
              </span>
              <StyledText fs={13} lh={15} fw={400} color="#5D5D5D">
                California, 10 Miles
              </StyledText>
            </div>
            <div className="flex flex-col xl:flex-row items-center">
              {['Certified Nurse Aid', 'Medication Technician'].map((val) => (
                <TagContainer key={val}>
                  <StyledText fs={13} fw={400} color="#000">
                    {val}
                  </StyledText>
                </TagContainer>
              ))}
            </div>
          </div>
          </div>
          <div className="w-full mt-12 xl:mt-0 xl:w-4/12">
            <StyledButton className="w-full py-4 mb-2" bg="#EA7112">
              <StyledText ls={0.3} fw={600} color="#FFF">
                Contact
              </StyledText>
            </StyledButton>
            <InformationContainer>
              <div className="flex items-center flex-1 mb-1">
                <span className="mr-2">
                  <PhoneIcon />
                </span>
                <StyledText fs={13} fw={400} lh={14} color="#000">
                  +23242352352
                </StyledText>
              </div>
              <div className="flex items-center flex-1 mb-1">
                <span className="mr-2">
                  <MailIconSm />
                </span>
                <StyledText fs={13} fw={400} lh={14} color="#000">
                  Jessicamadela@gmail.com
                </StyledText>
              </div>
              <div className="flex items-center flex-1 mb-1">
                <span className="mr-2">
                  <HomeIcon />
                </span>
                <StyledText fs={13} fw={400} lh={14} color="#000">
                  Lorem Ipsum is the address
                </StyledText>
              </div>
            </InformationContainer>
          </div>
        </div>
      {/* </div> */}
      <div className="flex w-full flex-1 items-center justify-between pt-7">
        <StyledText fw={400} color="#000">
          Add Notes
        </StyledText>
        <StyledButton bg="#363636" className="py-4 px-14">
          <StyledText ls={0.3} color="#fff">
            Save
          </StyledText>
        </StyledButton>
      </div>
    </Container>
  );
};

export default SingleEmployee;

const Container = styled.div`
  padding: 29px 36px;
  background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: 0px 0px 20px #00000010;
  margin-bottom: 26px;
`;

const StyledImage = styled.img`
  width: 90px;
  height: 90px;
  border-radius: 100%;
  object-fit: cover;
`;

const DescriptionContainer = styled(StyledText)`
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  line-clamp: 2;
  -webkit-box-orient: vertical;
  margin-bottom: 5px;
`;

const TagContainer = styled.div`
  padding: 5px 12px;
  background-color: #f2f2f2;
  border-radius: 2px;
  margin-right: 8px;
  margin-bottom: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
`;

const InformationContainer = styled.div`
  width: 100%;
  padding: 22px;
  background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: 0px 0px 20px #00000010;
  overflow: hidden;
`;

