import { FC } from 'react';
import styled from 'styled-components';
import { StyledButton, StyledText } from '../../../shared/StyledComponents';

const PlansSection: FC = () => {
  return (
    <div className="mx-auto flex flex-col items-center mt-20 pb-28">
      <StyledText fs={49} fw={600} ls={0.87} lh={49} color="#DA3B19">
        New to CaringDay?
      </StyledText>
      <StyledText fs={20} fw={500} color="#727272" className="mt-8 sm:mt-12 ml-4 sm:ml-0">
        Select the plan according to your needs.
      </StyledText>
      <div className="flex flex-col justify-center mx-auto  xl:flex-row items-center mt-20">
        <SilverCard className='sm:w-96'>
          <StyledButton className="px-8 py-5 w-full h-16" bg="#EA7112">
            <StyledText fs={20} fw={700} ls={1.4} color="white">
              Silver
            </StyledText>
          </StyledButton>
          <StyledText
            fs={55}
            fw={600}
            ls={0.87}
            color="#EA7112"
            className="mt-24 text-center"
          >
            $59.95
          </StyledText>
          <StyledText
            fs={20}
            fw={500}
            color="#727272"
            className="mt-8 text-center"
          >
            Per month
          </StyledText>
          <StyledText fs={20} fw={500} lh={30} className="mt-10 text-center">
            <li><span>Cancel anytime</span></li>
            <li><span>Unlimited searches for
            <br />
            candidates</span></li>
            <li><span>No referral fees</span></li>
            <li><span>Sign Up Now</span></li>
          </StyledText>
          <StyledText
            fs={25}
            fw={600}
            ls={0.75}
            color="#DA3B19"
            className="mt-14 text-center"
          >
            SUBSCRIBE
          </StyledText>
        </SilverCard>
        <GoldCard className="mt-20 sm:mx-12">
          <StyledButton className="px-8 py-5 w-full h-20" bg="#EA7112">
            <StyledText fs={30} fw={700} ls={1.2} color="white">
              Gold
            </StyledText>
          </StyledButton>
          <StyledText
            fs={60}
            fw={600}
            ls={0.87}
            color="#EA7112"
            className="mt-24 text-center"
          >
            $149.95
          </StyledText>
          <StyledText
            fs={20}
            fw={500}
            color="#727272"
            className="mt-10 text-center"
          >
            Every 3 months
          </StyledText>
          <StyledText fs={20} fw={500} lh={30} className="mt-12 text-center">
          <li><span>Cancel anytime</span></li>
          <li><span>Unlimited searches for
            candidates</span></li>
            <li><span>No referral fees</span></li>
            <li><span>Sign Up Now</span></li>
          </StyledText>
          <StyledText
            fs={25}
            fw={600}
            ls={0.75}
            color="#DA3B19"
            className="mt-14 text-center"
          >
            SUBSCRIBE
          </StyledText>
        </GoldCard>
        <DiamondCard className='sm:w-96'>
          <StyledButton className="px-8 py-5 w-full h-16" bg="#EA7112">
            <StyledText fs={20} fw={700} ls={1.4} color="white">
              Diamond
            </StyledText>
          </StyledButton>
          <StyledText
            fs={55}
            fw={600}
            ls={0.87}
            color="#EA7112"
            className="mt-24 text-center"
          >
            $299.95
          </StyledText>
          <StyledText
            fs={20}
            fw={500}
            color="#727272"
            className="mt-8 text-center"
          >
            Per year
          </StyledText>
          <StyledText fs={20} fw={500} lh={30} className="mt-10 text-center">
            <li><span>Cancel anytime</span></li>
            <li><span>Unlimited searches for
            candidates</span></li>
            <li><span>No referral fees</span></li>
            <li><span>Sign Up Now</span></li>
          </StyledText>
          <StyledText
            fs={25}
            fw={600}
            ls={0.75}
            color="#DA3B19"
            className="mt-14 text-center"
          >
            SUBSCRIBE
          </StyledText>
        </DiamondCard>
      </div>
    </div>
  );
};

export default PlansSection;

const SilverCard = styled.div`
  max-width: 354px;
  height: 528px;
  background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: 0px 0px 40px #00000012;
  background: white;
  padding: 20px;
  margin-bottom: 20px;
  li {
    color: red;
  };
  span {
    color:black;
  }
`;
const GoldCard = styled.div`
  max-width: 398px;
  height: 595px;
  background: white;
  background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: 0px 0px 20px #00000012;
  // margin: 0px 70px;
  padding: 20px;
  margin-bottom: 20px;
  li {
    color: red;
  };
  span {
    color:black;
  }
`;
const DiamondCard = styled.div`
  max-width: 354px;
  height: 528px;
  background: white;
  background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: 0px 0px 20px #00000012;
  padding: 20px;
  margin-bottom: 20px;
  li {
    color: red;
  };
  span {
    color:black;
  }
`;
