import React, { FC } from 'react';
import styled from 'styled-components';
import { StyledText } from '../../../shared/StyledComponents';

interface PropsTypes {
  label: string;
  isActive: boolean;
  onChange: () => void;
}

const RadiusSelect: FC<PropsTypes> = ({ label, isActive, onChange }) => {
  return (
    <div className="w-4/12 pr-2 cursor-pointer" onClick={onChange}>
      <Container>
        <StyledText
          fs={15}
          lh={20}
          fw={400}
          color={isActive ? '#EA7112' : '#000'}
        >
          {label}
        </StyledText>
      </Container>
    </div>
  );
};

export default RadiusSelect;

const Container = styled.div`
  width: 100%;
  padding: 11px 28px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #f2f2f2;
`;
