import { FC } from 'react';
import styled from 'styled-components';
import { HomeIcon, LocationIcon, MailIconSm, PhoneIcon } from '../../../icons';
import { StyledButton, StyledText } from '../../../shared/StyledComponents';

const SingleJob: FC = () => {
  return (
    <Container>
      <div className="lg:flex pb-7 border-b border-gray-100">
        <div className="lg:flex-1 lg:flex w-full">
          <div className="w-full lg:w-8/12 lg:pr-12">
            <StyledText fs={20} lh={30} fw={400} color="#EA7112">
              Health Care Job
            </StyledText>
            <DescriptionContainer>
              <StyledText fs={13} lh={15} fw={400} color="#5D5D5D">
                Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed
                diam nonumy eirmod tempor invidunt ut labore et dolore magna
                aliquyam erat, sed diam
              </StyledText>
            </DescriptionContainer>
            <div className="flex flex-1 items-center mb-3">
              <span className="mr-1">
                <LocationIcon />
              </span>
              <StyledText fs={13} lh={15} fw={400} color="#5D5D5D">
                California, 10 Miles
              </StyledText>
            </div>
            <StyledText
              className="mb-1"
              fs={13}
              lh={15}
              fw={500}
              color="#EA7112"
            >
              Type Of Job
            </StyledText>
            <StyledText className="mb-5" fs={13} lh={15} fw={400} color="#000">
              Certified Nurse Aid, Medication Technician, License Practice Nurse
            </StyledText>
            <div className="flex items-center">
              <StyledText
                className="mb-1"
                fs={13}
                lh={15}
                fw={500}
                color="#EA7112"
              >
                Facility
              </StyledText>
              <StyledImage
                className="ml-3 mr-1"
                src="https://c4.wallpaperflare.com/wallpaper/587/394/514/anime-narcissu-shinohara-himeko-wallpaper-preview.jpg"
                alt="user"
              />
              <StyledText className="mb-1" fw={400} color="#000">
                John wa
              </StyledText>
            </div>
          </div>
          <div className="w-full lg:w-4/12 mt-12 lg:mt-0 flex flex-col">
            <InformationContainer>
              <div className="flex items-center flex-1 mb-1">
                <StyledText fs={15} fw={600} lh={20} color="#EA7112">
                  Contact Details
                </StyledText>
              </div>
              <div className="flex items-center flex-1 mb-1">
                <span className="mr-2">
                  <PhoneIcon />
                </span>
                <StyledText fs={13} fw={400} lh={14} color="#000">
                  +23242352352
                </StyledText>
              </div>
              <div className="flex items-center flex-1 mb-1">
                <span className="mr-2">
                  <MailIconSm />
                </span>
                <StyledText fs={13} fw={400} lh={14} color="#000">
                  Jessicamadela@gmail.com
                </StyledText>
              </div>
              <div className="flex items-center flex-1 mb-1">
                <span className="mr-2">
                  <HomeIcon />
                </span>
                <StyledText fs={13} fw={400} lh={14} color="#000">
                  Lorem Ipsum is the address
                </StyledText>
              </div>
            </InformationContainer>
          </div>
        </div>
      </div>
      <div className="flex w-full flex-1 items-center justify-between pt-5">
        <StyledText fw={400} color="#000">
          Add Notes
        </StyledText>
        <StyledButton bg="#363636" className="py-4 px-14">
          <StyledText ls={0.3} color="#fff">
            Save
          </StyledText>
        </StyledButton>
      </div>
    </Container>
  );
};

export default SingleJob;

const Container = styled.div`
  padding: 29px 31px;
  background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: 0px 0px 20px #00000010;
  margin-bottom: 26px;
`;

const StyledImage = styled.img`
  width: 30px;
  height: 30px;
  border-radius: 100%;
  object-fit: cover;
`;

const DescriptionContainer = styled(StyledText)`
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  line-clamp: 2;
  -webkit-box-orient: vertical;
  margin-bottom: 5px;
`;

const InformationContainer = styled.div`
  width: 100%;
  padding: 22px;
  background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: 0px 0px 20px #00000010;
  overflow: hidden;
`;
