import { FC } from 'react';
import styled from 'styled-components';
import { StyledButton, StyledText } from '../../../shared/StyledComponents';
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from 'yup';
import HookTextGlobalInput from '../../../shared/hookForm/HookTextGlobalInput';

interface FormTypes {
  password: string;
  confirm_password: string;
}

const schema = yup.object().shape({
  password: yup.string().required('password is required!'),
  confirm_password: yup.string().required('password is required!'),
});

const defaultValues = {
  password: '',
  confirm_password: '',
};

const NewPasswordForm: FC = () => {
  const {
    handleSubmit,
    control,
    formState: { errors },
    reset,
  } = useForm<FormTypes>({
    resolver: yupResolver(schema),
    mode: 'onChange',
    defaultValues,
  });

  const submitForm = (formVales: FormTypes) => {
    console.log(formVales);
    reset(defaultValues);
  };

  return (
    <form onSubmit={handleSubmit(submitForm)}>
      <div className="container mx-auto flex flex-col items-center justify-center mt-32 lg:mt-6 py-28">
        <Container className="w-8/12 lg:w-5/12 mb-8">
          <div className="w-full mb-9">
            <StyledText fs={20} lh={39} color="#DA3B19">
              New Password
            </StyledText>
            <StyledText fs={13} lh={17} fw={400} color="#313131">
              Please Enter your new password.
              <br />
              Password must include an alphabet, a digit and one special
              character.
            </StyledText>
          </div>
          <div className="w-full flex mb-3">
            <HookTextGlobalInput
              name="password"
              control={control}
              errors={errors}
              label="Password"
            />
          </div>
          <div className="w-full flex mb-5">
            <HookTextGlobalInput
              name="confirm_password"
              control={control}
              errors={errors}
              label="Confirm Password"
            />
          </div>
          <div className="flex w-full items-center justify-center">
            <StyledButton type="submit" className="px-12 py-4">
              <StyledText fs={17} fw={700} ls={0.3} color="white">
                Done
              </StyledText>
            </StyledButton>
          </div>
        </Container>
      </div>
    </form>
  );
};

export default NewPasswordForm;

const Container = styled.div`
  padding: 32px;
  background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: 0px 0px 40px #00000012;
`;
