import { FC } from 'react';
import styled from 'styled-components';
import { StyledButton, StyledText } from '../../../shared/StyledComponents';
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from 'yup';
import Checkbox from '../../../shared/form/Checkbox';
import { includes, reject } from 'lodash';
import HookInputWithIcon from '../../../shared/hookForm/HookInputWithIcon';
import RadiusSelect from './RadiusSelect';
import { Link } from 'react-router-dom';

interface FormTypes {
  zip: string;
  radius: string | number;
  staff_type: number[];
}

const schema = yup.object().shape({
  zip: yup.string(),
  radius: yup.number(),
  staff_type: yup.array().of(yup.number()),
});

const defaultValues = {
  zip: '',
  radius: '',
  staff_type: [],
};

const FilterSection: FC = () => {
  const {
    handleSubmit,
    control,
    formState: { errors },
    setValue,
    watch,
    reset,
  } = useForm<FormTypes>({
    resolver: yupResolver(schema),
    mode: 'onChange',
    defaultValues,
  });

  const staffTypes = watch('staff_type');
  const radius = watch('radius');

  const submitSearch = (formData: any) => {
    console.log(formData);
    reset(defaultValues);
  };

  return (
    <form onSubmit={handleSubmit(submitSearch)} className="w-full">
      <Container className="container mx-auto sm:px-24 sm:py-12">
        <div className="w-full mb-8 flex items-center justify-center">
          <StyledText fs={29} lh={35} ls={0.6} fw={700} color="#DA3B19">
            Employer
          </StyledText>
        </div>
        <div className="w-full flex flex-wrap mb-10">
          <div className="w-full lg:w-4/12 pr-5 ">
            <div className="w-full mb-6">
              <StyledText
                fs={20}
                lh={25}
                className="mb-3"
                color="#EA7112"
                fw={600}
              >
                Search Area
              </StyledText>
              <HookInputWithIcon
                name="zip"
                type="number"
                control={control}
                errors={errors}
                label="Enter Zip Code"
              />
            </div>
            <div className="w-full">
              <StyledText
                fs={17}
                lh={25}
                className="mb-3"
                color="#000"
                fw={600}
              >
                Search Radius
              </StyledText>
              <div className="w-full flex flex-col sm:flex-row flex-wrap">
                {radiusList?.map((val) => (
                  <RadiusSelect
                    key={val.value}
                    label={val.label}
                    isActive={val.value === radius}
                    onChange={() =>
                      setValue('radius', val.value, {
                        shouldDirty: true,
                        shouldValidate: true,
                      })
                    }
                  />
                ))}
              </div>
            </div>
          </div>
          <div className="w-full lg:w-8/12 lg:pl-5">
            <div className="w-full">
              <StyledText
                fs={20}
                lh={30}
                className="mb-3"
                color="#EA7112"
                fw={600}
              >
                Type Of Staff
              </StyledText>
              <div className="flex flex-wrap">
                {jobTypesList?.map((val) => (
                  <div
                    className="sm:w-6/12 flex items-center mb-4 pr-4"
                    key={val.value}
                  >
                    <Checkbox
                      checked={includes(staffTypes, val.value)}
                      onChange={() => {
                        if (includes(staffTypes, val.value)) {
                          setValue(
                            'staff_type',
                            reject(staffTypes, (o) => o === val.value),
                            { shouldDirty: true, shouldValidate: true }
                          );
                        } else {
                          setValue('staff_type', [...staffTypes, val.value], {
                            shouldDirty: true,
                            shouldValidate: true,
                          });
                        }
                      }}
                    />
                    <StyledText color="#000" fs={15} lh={20} fw={400} ls={0}>
                      {val.label}
                    </StyledText>
                  </div>
                ))}
              </div>
            </div>
          </div>
        </div>
        <Link to="/employeelist">
        <div className="w-full flex items-center justify-center">
          <StyledButton type="submit" bg="#EA7112" className="py-4 px-24 mb-8 sm:mb-0">
            <StyledText ls={0.3} color="#fff">
              Search
            </StyledText>
          </StyledButton>
        </div>
        </Link>
      </Container>
    </form>
  );
};

export default FilterSection;

const Container = styled.div`
  flex: 1px;
  // padding: 59px 100px;
  background: #ffffff;
  box-shadow: 0px 0px 40px #00000010;
`;

const radiusList = [
  {
    value: 10,
    label: '10 Miles',
  },
  {
    value: 25,
    label: '25 Miles',
  },
  {
    value: 50,
    label: '50 Miles',
  },
];

const jobTypesList = [
  {
    value: 1,
    label: 'Direct Care Aid',
  },
  {
    value: 2,
    label: 'Certified Nurse Assistant (CNA)',
  },
  {
    value: 3,
    label: 'Medication Technician',
  },
  {
    value: 4,
    label: 'Registered Nurse (RN)',
  },
  {
    value: 5,
    label: 'License Practice Nurse (LPN)',
  },
  {
    value: 6,
    label: 'Maintenance',
  },
  {
    value: 7,
    label: 'Administrator - Skilled Nursing Unit',
  },
  {
    value: 8,
    label: 'Administrator - Personal Care Home',
  },
  {
    value: 9,
    label: 'Administrator - Secure Demantai unit',
  },
  {
    value: 10,
    label: 'Administrator - Assisted Living Facility',
  },
  {
    value: 11,
    label: 'Cook',
  },

  {
    value: 12,
    label: 'Pharmacist',
  },
  {
    value: 13,
    label: 'Psychologist',
  },
  {
    value: 14,
    label: 'Nursing Assistant',
  },
];
